import React, {  useEffect } from 'react';
import { connect } from 'react-redux'
import { validMail } from '../redux/constants/other'
import {getOther} from '../redux/actions/app';

const Main = React.memo(
    (props) =>{
        const { authenticated } = props.user;
        document.title = 'Восстановление пароля'
        useEffect(async ()=>{
            if(window.rerun!=undefined)
                await window.rerun()
            if(authenticated)
                props.history.push('/')
            window.startSignin()
            setTimeout(()=>{
                if(document.getElementsByName('Email')[0]!=undefined){
                    if(document.getElementsByName('Email')[0]!=undefined)
                        document.getElementsByName('Email')[0].type = 'email';
                    document.getElementsByClassName('t-form__submit')[0].onclick = async()=>{
                        if(validMail(document.getElementsByName('Email')[0].value)){
                            let data = await getOther({name: 'ВосстановлениеПароля', data: {email: document.getElementsByName('Email')[0].value}})
                            if(data=='ok')
                                alert('Вам выслано письмо')
                            else
                                alert('Проверьте правильность почты')
                        } else {
                            if(!validMail(document.getElementsByName('Email')[0].value))
                                alert('Проверьте правильность почты')
                        }
                    }
                }
            },1000)
        },[]);
        return (
            <>
                {/* T702 */}
                <div className="t702" style={{textAlign: 'center', display: 'inline-block'}}>
                        <div className="t-width_6">
                            <div className="t702__wrapper">
                                <div className="t702__text-wrapper t-align_center">
                                    <div className="t702__title t-title t-title_xxs" style={{}}>
                                        <div style={{fontSize: '20px'}} data-customstyle="yes">Восстановление пароля</div>
                                    </div>
                                </div>
                                <div id="form95019306" name="form95019306" role="form" action method="POST" data-formactiontype={0} data-inputbox=".t-input-group" className="t-form js-form-proccess t-form_inputs-total_1 " data-success-callback="t702_onSuccess">
                                    {/* NO ONE SERVICES CONNECTED */}
                                    <div className="js-successbox t-form__successbox t-text t-text_md" style={{display: 'none'}} />
                                    <div className="t-form__inputsbox">
                                        <div className="t-input-group t-input-group_em" data-input-lid={1495810354468}>
                                            <div className="t-input-block">
                                                <input type="text" name="Email" className="t-input js-tilda-rule " placeholder="E-mail" data-tilda-rule="email" style={{color: '#000000', border: '1px solid #c9c9c9', borderRadius: '5px', MozBorderRadius: '5px', WebkitBorderRadius: '5px'}} />
                                                <div className="t-input-error" />
                                            </div>
                                        </div>
                                        <div className="t-form__errorbox-middle">
                                            <div className="js-errorbox-all t-form__errorbox-wrapper" style={{display: 'none'}}>
                                                <div className="t-form__errorbox-text t-text t-text_md">
                                                    <p className="t-form__errorbox-item js-rule-error js-rule-error-all" />
                                                    <p className="t-form__errorbox-item js-rule-error js-rule-error-req" />
                                                    <p className="t-form__errorbox-item js-rule-error js-rule-error-email" />
                                                    <p className="t-form__errorbox-item js-rule-error js-rule-error-name" />
                                                    <p className="t-form__errorbox-item js-rule-error js-rule-error-phone" />
                                                    <p className="t-form__errorbox-item js-rule-error js-rule-error-string" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="t-form__submit">
                                            <button type="submit" className="t-submit" style={{color: '#ffffff', backgroundColor: '#d81b60', borderRadius: '2px', MozBorderRadius: '2px', WebkitBorderRadius: '2px'}}>Отправить</button>
                                        </div>
                                    </div>
                                    <div className="t-form__errorbox-bottom">
                                        <div className="js-errorbox-all t-form__errorbox-wrapper" style={{display: 'none'}}>
                                            <div className="t-form__errorbox-text t-text t-text_md">
                                                <p className="t-form__errorbox-item js-rule-error js-rule-error-all" />
                                                <p className="t-form__errorbox-item js-rule-error js-rule-error-req" />
                                                <p className="t-form__errorbox-item js-rule-error js-rule-error-email" />
                                                <p className="t-form__errorbox-item js-rule-error js-rule-error-name" />
                                                <p className="t-form__errorbox-item js-rule-error js-rule-error-phone" />
                                                <p className="t-form__errorbox-item js-rule-error js-rule-error-string" />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <style dangerouslySetInnerHTML={{__html: "\n                            #rec95019306 input::-webkit-input-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 input::-moz-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 input:-moz-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 input:-ms-input-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 textarea::-webkit-input-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 textarea::-moz-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 textarea:-moz-placeholder {color:#000000; opacity: 0.5;}\t#rec95019306 textarea:-ms-input-placeholder {color:#000000; opacity: 0.5;}\n                        " }} />
                            </div>
                        </div>
                </div>
                <style dangerouslySetInnerHTML={{__html: "\n            #rec95019306 .t-btn[data-btneffects-first],\n            #rec95019306 .t-btn[data-btneffects-second],\n            #rec95019306 .t-submit[data-btneffects-first],\n            #rec95019306 .t-submit[data-btneffects-second] {\tposition: relative; overflow: hidden; -webkit-transform: translate3d(0,0,0); transform: translate3d(0,0,0);\n            }\n        " }} />
            </>
        );
    })

function mapStateToProps (state) {
    return {
        user: state.user
    }
}

export default connect(mapStateToProps)(Main);
